import { Component, OnInit } from '@angular/core';
import { NavController, ModalController } from '@ionic/angular';
import { ImageModalPage } from '../../image-modal/image-modal.page';
import { ScreenOrientation } from '@ionic-native/screen-orientation/ngx';

@Component({
  selector: 'app-denpasar-details',
  templateUrl: './denpasar-details.page.html',
  styleUrls: ['./denpasar-details.page.scss'],
})
export class DenpasarDetailsPage implements OnInit {

  images = ['bali/regions/denpasar/denpasar1','bali/regions/denpasar/denpasar2','bali/regions/denpasar/denpasar3']

  sliderOpts = {
    zoom: false,
    slidesPerView: 1.5,
    centeredSlides: false,
    spaceBetween: 20
  };

  constructor(private navController: NavController,
    private modalController: ModalController,
    private screenOrientation : ScreenOrientation) { 
      this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.PORTRAIT_PRIMARY);
    }

  ngOnInit() {
    //
  }

  openPreview(img){
    this.modalController.create({
      component: ImageModalPage,
      componentProps: {
        img: img
      }
    }).then(modal => modal.present());
  }


}
